import React from "react";
import { Link } from "react-router-dom";
import "./header.scss";
import Cart from "../CartIcon/index";

const Header = () => {
  return (
    <nav className="nav-menu container">
      <div className="logo">
        <Link to="/"> STORE</Link>
      </div>
      <ul>
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/shop">Shop</Link>
        </li>
      </ul>

      <Cart />
    </nav>
  );
};

export default Header;
